
/* //////////////////////////////////////////////////////////////////// */

.worker-dashboard{
    height: 100%;
    min-height :100%;
    background: transparent;
}

.worker-dashboard h2{
  margin: 3rem 0;
  border-bottom: 2px solid navy;
    padding-bottom: 1.5rem;
}
.container{
  margin-top:0;
}
.worker-dashboard hr{
  margin: 0.5rem 0;
}

.worker-dashboard .admin-select-option{
padding-bottom: 1rem;
font-weight: 700;
}

.admin-option{
    border: 1px solid navy;
    height: 10rem;
    display: flex;
    max-width: 25rem;
    flex: 1 1 25rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    margin: 1.5rem 0.8rem;
    cursor: pointer;
    border-radius: 9rem;
    padding: 2rem;
    transition: all 0.4s ease-in;
    font-weight: bold;
}

.admin-option:hover{
  transform: scale(1.05);
  border: 2px solid #fff;
  box-shadow: 0px 0px 10px 0px rgba(0,255,0,0.6);
}

admin-option2{
  font-size: 1.6rem;
}


.admin-option h3{
  font-size: 2rem;
  font-weight: bold;
}
.admin-options-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-width: 21rem;
  max-width: 115rem;
  padding: 2rem;
}

.textWhite{
  transition: color 0.5s ease-in-out;
  font-size: 2rem;
}
.textWhite:hover{
 color: #fff;
}

.top-btn{
  width: 90%;
  position: relative;
  padding-top: 8rem ;
  left: 7%;
}









@media (max-width: 690px){
  
  .admin-option{
    padding: 0rem;
    flex : 1 1 18rem;

  }

}