.parentBoxPUC{
    height: 80vh;
    margin-top: 6rem;
    max-width: 1200px;
    width: 85%;
    margin: 5rem auto;
    background-color: #fff;
}

.boxPUC{
    border : 1px solid black ;
    display: inline-block;
    width: 80%;
    text-align: center;

    position: relative;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 4rem 2rem;
    background: linear-gradient(to bottom, rgba(0,0,0,0.4) , rgba(0,0,0,0.1));
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
}

.boxPUC h2{
    margin: 1rem 0;
    font-size: 2rem;
}

.boxPUC p{
    font-size: 1.6rem;
}

.adminLink {
    display: inline-block;
    border: 0.1rem solid #000;
    margin: 2rem;
    background: rgb(53, 251, 5);
    border-radius: 10rem;
    width: 200px;
    font-size: 1.4rem;
    font-weight: 700;
    padding: 0.4rem 0.4rem;
    /* box-shadow: 0px  0px 6px 0px rgba(170, 170, 170, 0.374); */
    transition: all 0.1s ease-in;
  }
  .adminLink:hover {
    transform: scale(1.1);
    background: rgb(0, 255, 0);
    box-shadow: 0px 0px 10px 0px rgba(170, 170, 170, 0.374);
  }

  @media (max-width:550px){
    
    .boxPUC{
        width: 85%;
        padding: 3rem 1.5rem;
           }
    
    .boxPUC h2{
        margin: 1rem 0;
        font-size: 1.6rem;
    }
    
    .boxPUC p{
        font-size: 1.2rem;
    }
    
    .adminLink {
        margin: 1.4rem;
        width: 125px;
        font-size: 1.2rem;
      }
  }