

/* //////////////////////////////////////////////////////////////////// */

#admin-home{
  background: url('../../images/Admin_Dashboard.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding-top: 4rem;
  top: 0%;
}

.admin-dashboard{
  padding-top: 3rem;
  min-height: 85vh;
  height: 100vh;
  background: transparent;
}

.admin-dashboard h2{
  margin: 3rem 0;
  border-bottom: 2px solid navy;
    padding-bottom: 1.5rem;
}

.admin-dashboard hr{
  margin: 0.5rem 0;
}

.admin-dashboard .admin-select-option{
padding-bottom: 1rem;
font-weight: 700;
}

.admin-option{
  border: 1px solid navy;
  height: 15rem;
  display: flex;
  max-width: 20rem;
  flex: 1 1 15rem;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  margin: 1rem;
  transition: all 0.5s ease-in-out;
}

.admin-option:hover{
transform: translateY(-12%);
background-color: linear-gradient(to right, #12FFF7, #B3FFAB);  
color: #fff;
}

.textWColor{
  transition: color 0.5s ease-in-out;
}
.textWColor:hover{
  color: #fff;
}

.admin-option Link, .admin-option a{
font-size: 2.2rem;
position: absolute;
height: 150px;
width: 200px;
display: flex;
align-items: center;
justify-content: center;
}


.admin-options-container{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
align-items: center;
min-width: 21rem;
max-width: 115rem;
padding: 2rem;
}


@media (max-width: 690px){
  
  .admin-option a{
    font-size: 2rem;
  }

  .container h2{
    font-size: 1.8rem;
  }
  .container p {
    font-size: 1.4rem;
  }
}
