
.fontWhite{
    color: #fff;
}

/* __________________________-- */

.main-menu{  
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 5rem;
    font-size: 1.6em;
    font-weight: 700;
    min-width: 100%;
    max-width: 1200px;
    padding: 0 14%;
    transition : all 1s ease-in;
    background-color: transparent;
}

.dark{
    /* background-color: #a8f0f5;   */
    background: #9796f0;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fbc7d4, #9796f0);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fbc7d4, #9796f0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: #fff !important;    
    transition : all 1s ease-in;
}

.main-menu .logo{
    font-size: 2.2rem;
}

.main-menu-options ul{
    width: 40rem;
    display: flex;
    justify-content: space-evenly;
}
    

.main-menu a{
    color: black;
    text-decoration: none;
}
.main-menu-options ul li, .main-menu-options ul Link{
    transition: all 0.3s ease-in;
}
.main-menu-options ul li:hover{
    color: rgb(53, 251, 5);
    text-decoration: underline;
    transform: scale(1.2);
}


.main-menu-options ul Link:hover{
    color: rgb(53, 251, 5);

}


ul{
    list-style: none;
}

.side-menu-options ul{
    display: flex;
    width: 25rem;
    justify-content: space-evenly;   
    
}


/* ##################################################################### */
.dropdown-toggle{
    display: none;
    cursor: pointer;
}

.dropdown-menu{
    position: absolute;
    top: 120%;
    right: 2%;
    /* left: 80%; */
    padding: 2rem 5rem;
    border: 1px solid rgba(0,0,0,0.4);
    background: linear-gradient(to top, rgba(0,0,0,0.4) , rgba(0,0,0,0.1));
    border-radius: 10px;
    z-index: 99;
    display: none;
}
.dropdown-menu .dropOption{
    padding: 0.5rem 0;
    transition: all 0.5 ease-in-out;
}
.dropdown-menu .dropOption:hover{
    color: #fff;
    transform: scale(1.2);
}

.dropdown-menu .dropOptionSp{
    margin: 1rem auto;
}

/* _________________________________ MEDIA QUERIES __________________________ */


@media (max-width:820px){
    .main-menu-options ul{
        width: 30rem;
        padding: 0 1rem;
    }
    
}

@media (max-width:750px){
    
    .main-menu-options , .side-menu-options{
        display: none;
    }
    .dropdown-toggle{
        display: block;
    }
    .main-menu .dropdown-menu-options a{
        color : #fff;
    }
}
@media (max-width:520px){
    .dropdown-menu{
        /* display: block; */
        right: -1%;
        width: 105%;
        top: 100%;
        background: linear-gradient(to top, rgba(0,0,0,0.5) , rgba(0,0,0,0.2));
        text-align: center;
    }
}
