/* ################################ Media queries ######################################## */

@media (max-width: 750px) {
  #home .home-text h1,
  .font-large {
    font-size: 3rem;
    text-align: start;
  }
  #home .home-text h4,
  .font-medium,
  .typewritter {
    font-size: 1.8rem;
  }
  #home .home-text p,
  .font-medium {
    font-size: 1.4rem;
    color: #000;
    text-align: start;
    align-items: start;
  }
  .knowMore,
  .adminDashboardBtn {
    margin: 1rem 3rem;
    font-size: 1.2rem;
    width: 15rem;
    padding: 0.5rem 0.6rem;
    border: 1px solid black;
  }

  .more-products-btn {
    font-size: 1.2rem;
    width: 15rem;
    padding: 0.5rem 0.6rem;
    border: 1px solid black;
  }

  #about .about-area{
    display: block;
  }

  #about .about-text{
    font-size: 1.4rem;
  }
}

/* @media(max-width : 520px ){
   
  } */
@media(max-width : 360px ){
    #home .home-text h1,
    .font-large {
      font-size: 2.5rem;
    }
    #home .home-text h4,
    .font-medium,
    .typewritter {
      font-size: 1.5rem;
    }
    #home .home-text p,
    .font-medium {
      font-size: 1.2rem;
    }

    #about .about-text{
        font-size: 1.2rem;
      }
  }


