.handleColor{
  transition: color 0.5s ease-in-out;
  font-size: 2rem;
}
.handleColor:hover{
  color: #fff;
}
/* //////////////////////////////////////////////////////////////////// */


.client-dashboard{
  background: transparent;
    min-height :100%;
    padding: 0 3rem;
    height: 100%;
    background: transparent;
}

.client-dashboard h2{
  margin: 3rem 0;
  border-bottom: 2px solid navy;
    padding-bottom: 1.5rem;
}

.client-dashboard hr{
  margin: 0.5rem 0;
}

.client-dashboard .admin-select-option{
padding-bottom: 1rem;
font-weight: 700;
}


.admin-option{
    border: 1px solid navy;
    height: 10rem;
    display: flex;
    max-width: 25rem;
    flex: 1 1 25rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    margin: 1.5rem 0.8rem;
    cursor: pointer;
    border-radius: 9rem;
    padding: 2rem;
    transition: all 0.4s ease-in;
    font-weight: bold;
}

.admin-option:hover{
  transform: scale(1.05);
  border: 2px solid #fff;
  box-shadow: 0px 0px 10px 0px rgba(0,255,0,0.6);
}

admin-option2{
  font-size: 1.6rem;
}


.admin-option h3{
  font-size: 2rem;
  font-weight: bold;
}
.admin-options-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-width: 21rem;
  max-width: 115rem;
  padding: 2rem;
}

